const Seccion = () => {

    const [selectedOption, setSelectedOption] = useState('');
    const [selectedOption2, setSelectedOption2] = useState('Divisa 2');
    const [texto, setTexto] = useState('');
    const [conversion, setConversion] = useState('');
    const [fecha, setFecha] = useState("");
    const [show, setShow] = useState(false);
    const [por1, setPor1] = useState({});
    const [por10, setPor10] = useState({});
    const [por100, setPor100] = useState({});
    const [en1, setEn1] = useState({});
    const [en10, setEn10] = useState({});
    const [en100, setEn100] = useState({});

    const obtenerDivisas = async (divisaA, divisaB, text) => {

        console.log(divisaA + " " + divisaB + " " + text);
        if (divisaA === undefined || divisaB === undefined || text === "") {
            alert("Debes llenar todos los campos");
        }
        else if (divisaA === divisaB) {
            setConversion("1");
            setPor1({
                title: '1 ' + divisaA,
                value: "1 " + divisaA
            });
            setPor10({
                title: '10 ' + divisaA,
                value: "10 " + divisaA
            });
            setPor100({
                title: '100 ' + divisaA,
                value: "100 " + divisaA
            });
            setEn1({
                title: '1 ' + divisaA,
                value: "1 " + divisaA
            });
            setEn10({
                title: '10 ' + divisaA,
                value: "10 " + divisaA
            });
            setEn100({
                title: '100 ' + divisaA,
                value: "100 " + divisaA
            });
            setShow(true);
            alert("Los resultados no son muy interesantes. Intenta con dos divisas diferentes :)");
        }
        else {

            const url = 'https://v6.exchangerate-api.com/v6/aac9b36d86d77623835d5a6c/pair/' + divisaA + '/' + divisaB;
            const res = await axios.get(url);
            console.log(res);
            if (res.data.result === "success") {
                let rate = res.data.conversion_rate;
                let valor = numeral(parseFloat(rate) * parseFloat(text)).format('0,0');
                setConversion(valor + "");
                let valorPor1 = rate.toFixed(5);
                let valorPor10 = (rate * 10).toFixed(5);
                let valorPor100 = (rate * 100).toFixed(5);
                let valor1EnRate = (1 / rate).toFixed(5);
                let valor10EnRate = (10 / rate).toFixed(5);
                let valor100EnRate = (100 / rate).toFixed(5);
                let date = (res.data.time_last_update_utc).split(" ", "4");
                setFecha(date[1] + " " + date[2] + " " + date[3]);

                setPor1({
                    title: '1 ' + divisaA,
                    value: valorPor1 + " " + divisaB
                });
                setPor10({
                    title: '10 ' + divisaA,
                    value: valorPor10 + " " + divisaB
                });
                setPor100({
                    title: '100 ' + divisaA,
                    value: valorPor100 + " " + divisaB
                });
                setEn1({
                    title: valor1EnRate + " " + divisaA,
                    value: '1 ' + divisaB
                });
                setEn10({
                    title: valor10EnRate + " " + divisaA,
                    value: '10 ' + divisaB
                });
                setEn100({
                    title: valor100EnRate + " " + divisaA,
                    value: '100 ' + divisaB
                });
                setShow(true);
            }
        }
    }

    const cambiarDivisas=()=>{
        let div1 = selectedOption;
        let div2 = selectedOption2;
        setSelectedOption(div2);
        setSelectedOption2(div1);
        let val1 = por1;
        let val10 = por10;
        let val100 = por100;
        let value01 = en1;
        let value001 = en10;
        let value0001 = en100;

        setPor1({
            title: value01.value,
            value: value01.title
        });
        setPor10({
            title: value001.value,
            value: value001.title
        });
        setPor100({
            title: value0001.value,
            value: value0001.title
        });
        setEn1({
            title: val1.value,
            value: val1.title
        });
        setEn10({
            title: val10.value,
            value: val10.title
        });
        setEn100({
            title: val100.value,
            value: val100.title
        });
        
    }

    const textoChange=(textoN)=>{
        setTexto(textoN);
        setConversion("");
        setFecha("");
        setPor1("");
        setPor10("");
        setPor100("");
        setEn1("");
        setEn10("");
        setEn100("");
    }
    return (
        <>
            <div className="card-section">
                <div className="container">
                    <div style={{ float: "right", marginRight: "2%", marginBottom: "2%", marginTop: "2%" }}>
                        <button className="btn botonChange" onClick={()=>cambiarDivisas()}><img src={change} className="imgChange"></img></button>
                    </div>
                    <div className="card-block bg-white mb30">

                        <div className="cards">
                            <div className="form__group field">
                                <input
                                    type="number"
                                    className="form__field"
                                    placeholder="Importe"
                                    name="importe"
                                    id="importe"
                                    min="0"
                                    onChange={(e) => {setTexto(e.target.value); setShow(false)}}
                                />
                                <label htmlFor="importe" className="form__label">Importe</label>
                            </div>
                            <label htmlFor="select1"> De:
                            <Select name='select1'
                                    value={selectedOption}
                                    onChange={(e) => {setSelectedOption(e); setShow(false)}}
                                    options={options}
                                    placeholder="Seleccione divisa"
                                />
                            </label>
                            <label htmlFor="select2"> A:
                            <Select name='select2'
                                    value={selectedOption2}
                                    onChange={(e) => {setSelectedOption2(e); setShow(false)}}
                                    options={options}
                                    placeholder="Seleccione divisa"
                                />
                            </label>
                        </div>
                        {show ?
                            <div>
                                <h6 className="rate">{texto} {selectedOption.value} =</h6>
                                <h1 className="divisa">{conversion} {selectedOption2.value}</h1>
                                <h6 className="rate">
                                    {por1.title} = {por1.value}
                                </h6>
                                <h6 className="rate">
                                    {en1.value} = {en1.title}
                                </h6>
                                <p className="rate texto">Ultima actualización: {fecha}</p>
                            </div> :
                            <></>}

                        <button className="btn btn-info mt-3 btm" onClick={() => obtenerDivisas(selectedOption.value, selectedOption2.value, texto)}>Convertir</button>
                    </div>
                </div>
            </div>

            {show ?
                <div className="container">
                    <table className="table table-bordered">
                        <thead>
                            <tr>
                                <th>{selectedOption.label} / {selectedOption.value}</th>
                                <th>{selectedOption2.label} / {selectedOption2.value}</th>
                            </tr>
                        </thead>
                        <tbody>
                                <tr>
                                    <td>{por1.title}</td>
                                    <td>{por1.value}</td>
                                </tr>
                                <tr>
                                    <td>{por10.title}</td>
                                    <td>{por10.value}</td>
                                </tr>
                                <tr>
                                    <td>{por100.title}</td>
                                    <td>{por100.value}</td>
                                </tr>
                                <tr>
                                    <td>{en1.title}</td>
                                    <td>{en1.value}</td>
                                </tr>
                                <tr>
                                    <td>{en10.title}</td>
                                    <td>{en10.value}</td>
                                </tr>
                                <tr>
                                    <td>{en100.title}</td>
                                    <td>{en100.value}</td>
                                </tr>
                        </tbody>
                    </table>
                </div>
                :
                <></>
            }
        </>
    )
}

export default Seccion;